/* General styles */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background: var(--bg);
}
a {
  text-decoration: none;
}

:root{
  --green: #8D9F8F;
  --light-gray: #C8CECE;
  --gray: #919392;
  --dark-gray: #5C5E5D;
  --text-gray: #515251;
  --bg: #EBF2F2;
  --logo: 136px;
  --logoMobile: 50px;
  --nav: 33px;
  --font: 'Cormorant Infant', serif;
}

/* Topbar and menu */
.top-bar{
  height: var(--logo);
  width: 100%;
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--green);
}

.top-bar__logo{
  font-family: var(--font);
  font-size: 85px;
  color: white;
  letter-spacing: 5px;
  font-weight: 300;
}

.top-bar__desktop-nav{
  width: 100%;
  height: var(--nav);
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  list-style: none;
  background: var(--light-gray);
}

.desktop-nav__item{
  font-size: 21px;
  border-right: 1px solid var(--dark-gray);
  padding-right: 20px;
}

.desktop-nav__item:last-of-type{
  border-right: none;
}

.desktop-nav__item a{
  color:var(--dark-gray);
  transition: color 0.3s;
}

.desktop-nav__item a:hover{
  color: var(--green);
}

/* Hero */
.hero{
  width: 100%;
  height: calc(100vh - var(--logo) - var(--nav)) ;
  position: relative;
  background: var(--bg);
}
.hero__shape-right{
  position: absolute;
  right: 0;
  height: calc(100vh - var(--logo) - var(--nav)) ;
}
.hero__shape-left{
  position: absolute;
  left: 0;
  height: 360px;
}

.hero__info{
  width: 77%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 30px 0 0 0;
}

.intro{
  position: relative;
  font-family: var(--font);
  line-height: 0.9;
}
.intro__name{
  font-size: 90px;
  position: relative;
  right: 80px;
  top:8px
}
.intro__name::after{
  content: '';
  display: block;
  width: 155px;
  height: 2px;
  transform: translate(430px, -13px);
  background: var(--gray);
}
.intro__surname{
  font-size: 105px;
}
.intro__name,.intro__surname{
  color: var(--gray);
  font-weight: 300;
}

.info__p{
  display: flex;
  margin: 30px 0 0 0;
  font-size: 20px;
}
.p-left{
  max-width: 316px;
  position: relative;
  margin: 20px 40px 0 0;
  text-align: right;
  font-weight: 700;
}

.p-right{
  max-width: 345px;
  margin: 70px 0 0 0;
}
.p-right,.p-left{
  line-height: 1.25;
  color: var(--text-gray)
}

/* Contact */
.contact-container{
  position: relative;
  min-height: calc(100vh - var(--logo) - var(--nav));
  background: var(--bg);

}
.contact-shape{
  position: absolute;
  bottom: 0;
  height:90%
}

.contact{
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 60px 0 0 0;
  color: var(--dark-gray);

}
.contact__map{
  width: 430px;
  height: 430px;
  border: 25px solid #E2D6C6;
}

.contact__info{
  margin: 0 0 217px 50px;
}
.contact__info-title{
  font-family: var(--font);
  font-weight: 400;
  font-size: 36px;
  position: relative;
  margin: 0 0 20px 0;
}
.contact__info-title::after{
  content: '';
  height: 1px;
  width: 150px;
  background: var(--dark-gray);
  position: absolute;
  bottom: 0;
  left: 7px;
}

.contact__info-p{
  font-size: 19px;
  margin: 0 0 10px 0;
}
.contact__info-p:nth-of-type(5){
  margin: 0;
}
.contact__info-shape{
  width: 15px;
  height: 15px;
  margin: 0 0 10px 0;
  border-radius: 50%;
  background: var(--gray);
}

.contact__social{
  display: flex;
  margin: 284px 0 0 120px;
}
.contact__social a{
  width: 120px;
  border-radius: 50%;
}
.contact__social a img{
  width: 100%;
  border-radius: 50%;
  transition: transform 0.3s;
}
.contact__social a img:hover{
  transform: rotate(30deg);
}

.top-bar__mobile{
  display: none;
}



/* RWD */
@media (max-width:850px){
  .hero__shape-right, .hero__shape-left, .top-bar__desktop-nav, .top-bar__logo{
    display: none;
  }
  .top-bar__mobile{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  .top-bar{
    height: var(--logoMobile);
  }
  .mobile__hamburger{
    position: fixed;
    width: 20px;
    height: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    margin: 0 30px 0 0;
  }
  .mobile__checkbox{
    position: fixed;
    width: 20px;
    height: 20px;
    margin: 0 30px 0 0;
    opacity: 0;
    z-index: 101;
    cursor: pointer;
  }
  .hamburger__line{
    width: 100%;
    height: 2px;
    background: white;
    transition: 0.3s;
  }
  .mobile__hamburger.active .hamburger__line-top{
    transform: translate(0, 9px) rotate(45deg);
  }
  .mobile__hamburger.active .hamburger__line-mid{
    opacity: 0;
  }
  .mobile__hamburger:hover .hamburger__line-bottom, .mobile__hamburger.active .hamburger__line-bottom {
    transform: translate(0, -9px) rotate(-45deg);
  }
  .mobile__menu{
    width: 100vw;
    height: 100vh;
    position: fixed;
    gap: 15px;
    top:0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--green);
    transform: translateX(100%);
    transition: 0.3s;
    z-index: -1;
  }
  .mobile__menu.active{
    transform: translateX(0);
  }
  .menu__cross{
    width: 20px;
    height: 20px;
    position: absolute;
    top: 40px;
    right: 40px;
    z-index: 100
  }
  .menu__mobile-nav{
    list-style: none;
    text-align: center;
  }
  .mobile-nav__item{
    margin: 0 0 20px 0;
    font-family: var(--font);
  }
  .menu__mobile-nav a{
    color: #fff;
    font-size: 25px;
    transition: 0.3s color;
  }
  .menu__mobile-nav a:hover{
    color: var(--dark-gray)
  }
  .menu__shape{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #E2D6C6;
  }
  .menu__logo{
    max-width: 197px;
    text-align: center;
    font-family: var(--font);
    font-weight: 300;
    font-size: 43px;
    color: #fff;
  }
  .intro{
    display: none;
  }
}